// ** React Imports
import { useState, useEffect, Fragment } from 'react'
import { Link } from 'react-router-dom'
import classnames from 'classnames'
import { useDispatch, useSelector } from 'react-redux'
// ** Custom Components
import { updateClient, updateContactInfo, getClient, getConatctInfo, deleteContactInfo } from '@src/views/apps/client/store/index.js'
import axios from '@src/configs/axios/axiosConfig'

import { X, Plus } from 'react-feather'
import Select from 'react-select'
import { useForm, useFieldArray, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { activeOrganization, orgUserId } from '@src/helper/sassHelper'
// ** Reactstrap Imports
import { Row, Col, Card, Label, Button, CardBody, CardText, Input, FormFeedback, CardTitle, CardHeader } from 'reactstrap'

// ** Styles
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

import 'react-slidedown/lib/slidedown.css'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/base/pages/app-invoice.scss'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const activeOrg = activeOrganization()
const userId = orgUserId()
const EditCard = () => {

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const colourStyles = {
    multiValue: (styles, { }) => {

      return {
        ...styles,
        backgroundColor: '#7367f0'
      }
    }
  }

  // ** States
  const MySwal = withReactContent(Swal)
  const phoneRegExp = /^[0-9\- ]{10,10}$/
  const zipcodeExp = /^[0-9\- ]{6,6}$/
  const store = useSelector(state => state.client)
  const dispatch = useDispatch()
  const [businessEntityOptions, setBusinessEntityOptions] = useState([])
  const [stateOptions, setStateOptions] = useState([])
  const [countryOptions, setCountryOptions] = useState([])
  const [currencyOptions, setCurrencyOptions] = useState([])
  const [gstRegistrationTypeOptions, setGstRegistrationTypeOptions] = useState([])
  const [clientInfo, setClientInfo] = useState([])
  const [contactId, setConatctId] = useState(null)
  const [clientDetails, setClientDetails] = useState({})
  const [userOptions, setUserOptions] = useState([])
  const [employeeDefaultValue, setEmployeeDefaultValue] = useState([])
  const [countryCode, setCountryCode] = useState(activeOrg.countryshortname)

  const schema = yup.object().shape({
    updatedBy: yup.string().default(userId),
    organization: yup.string().default(activeOrg.id),
    contactPersonName: yup.string().required("Please Enter a Contact Person Name"),
    registrationNo: yup.string().default("").nullable(),
    employeeList: yup.array().default([]),
    name: yup.string().required("Please Enter Business Name.").default(''),
    referredby: yup.string().nullable().default(''),
    contactNumber: yup.string().default('').matches(phoneRegExp, { message: 'Phone number is not valid', excludeEmptyString: true }).nullable(),
    email: yup.string().email("Please Enter valid Email").default("").nullable(),
    countryCode: yup.string().default(activeOrg.countryshortname),
    organizationName: yup.string().default(activeOrg.name),
    businessEntity: yup.string().when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please Select Business Entity') }),
    gstRegistrationType: yup.string().nullable(),
    gstin: yup.string().nullable(),
    placeOfSupply: yup.string().default(''),
    placeOfSupplyOther: yup.string().default(activeOrg.stateothers || ''),
    currency: yup.string().default(activeOrg.currencyid),
    billingCountryCode: yup.string().default(''),
    billingAddressLine1: yup.string().nullable(),
    billingAddressLine2: yup.string().nullable(),
    billingAddressCity: yup.string().nullable(),
    billingAddressState: yup.string().default(''),
    billingAddressStateOthers: yup.string().default(activeOrg.stateothers || ''),
    billingAddressCountry: yup.string().default(activeOrg.countryid),
    billingAddressZip: yup.string().matches(zipcodeExp, { message: 'Zip Code is not valid', excludeEmptyString: true }).nullable(''),
    contact_info: yup.array().of(
      yup.object().shape({
        createdBy: yup.string().default(userId),
        name: yup.string().nullable(),
        email: yup.string().email().nullable(),
        designation: yup.string().nullable(),
        adharnumber: yup.string().nullable(),
        dinnumber: yup.string().nullable(),
        pannumber: yup.string().nullable(),
        contactnumber: yup.string().nullable().matches(phoneRegExp, { message: 'Phone number is not valid', excludeEmptyString: true })
      })
    )
  })

  const { handleSubmit, reset, control, formState: { errors }, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })
  const id = localStorage.getItem('clientId')
  const { fields, append, remove } = useFieldArray({ name: 'contact_info', control, keyName: 'rowid' })

  const saveContactInfo = async () => {

    const data = { rows: clientInfo }
    await dispatch(updateContactInfo(data))
  
  }

  const onSubmit = async (data) => {

    setBlock(true)

    const temp = data.contact_info || []
    delete data.contact_info
    const id = clientDetails.id
    await dispatch(updateClient({ data, id }))

    setClientInfo(predata => ([...predata, ...temp]))

  }

  const deletefun = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteContactInfo(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Contact has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const addItem = (() => {
    append({ id: '', name: '', email: '', contactnumber: '', contactid: id, organizationid: activeOrg.id, designation: '', primarystatus: false, adharnumber: '', dinnumber: '', pannumber: '' })
  })

  const removeItem = async (ind) => {
    const tempid = control._formValues.contact_info[ind].id
    let flg
    if (tempid !== undefined) {
      flg = await deletefun(tempid)
    }
    if (flg) {
      remove(ind)
    }
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data
      setUserOptions(arr.organizationusers)
    }).catch((err) => {
      console.log(err)
    })

  }

  const getBusineessEntity = () => {
    axios.post('/businessentities/list').then(response => {
      const arr = response.data
      setBusinessEntityOptions(arr.businessentities)
    })
  }

  const getCountries = () => {
    axios.post('/countries/dropdown').then(response => {
      const arr = response.data
      setCountryOptions(arr.countries)
    })
  }

  const getStates = () => {
    axios.post('/states/list').then(response => {
      const arr = response.data
      setStateOptions(arr.states)
    })
  }

  const getCurrency = () => {
    axios.post('/currencies/dropdown').then(response => {
      const arr = response.data
      setCurrencyOptions(arr.currencies)
    })
  }

  const getGSTRegType = () => {
    axios.post('/gstregistrationtypes/list').then(response => {
      const arr = response.data
      setGstRegistrationTypeOptions(arr.gstregistrationtypes)
    })
  }

  const getClientInfo = async () => {
    await dispatch(getConatctInfo(contactId))

  }

  const getClientData = async () => {
    const client = await dispatch(getClient(id))
    setClientDetails(client.payload)
  }

  // handle onChange event of the dropdown
  const handleEmployeeChange = (list) => {
    const tempArr = list ?? []
    const temp = userOptions.filter(({ id: id1 }) => tempArr.some(id2 => id2 === id1))
    setEmployeeDefaultValue(temp)
  }

  const handleEmployeeChange1 = (e) => {
    const temparr = Array.isArray(e) ? e.map(x => x.id) : []
    const temp = userOptions.filter(({ id: id1 }) => temparr.some(id2 => id2 === id1))
    setEmployeeDefaultValue(temp)
    setValue('employeeList', temparr)
  }

  useEffect(() => {

    if (Object.keys(clientDetails).length > 0) {
      reset({
        updatedBy: userId,
        uniqueIdentity: clientDetails.uniqueidentity,
        contactPersonName: clientDetails.contactpersonname,
        organization: clientDetails.organizationid,
        name: clientDetails.name,
        referredby: clientDetails.referredby,
        registrationNo: clientDetails.registrationno,
        contactNumber: clientDetails.contactnumber,
        businessEntity: clientDetails.businessentityid,
        email: clientDetails.email,
        gstRegistrationType: clientDetails.gstregistrationtypeid,
        gstin: clientDetails.gstin || '',
        placeOfSupply: clientDetails.placeofsupplyid || activeOrg.stateid,
        currency: clientDetails.currencyid || activeOrg.currencyid || '',
        billingAddressLine1: clientDetails.billingaddressline1 || '',
        billingAddressLine2: clientDetails.billingaddressline2 || '',
        billingAddressCountry: clientDetails.billingaddresscountry || activeOrg.countryid,
        billingAddressState: clientDetails.billingaddressstate || activeOrg.stateid,
        billingAddressZip: clientDetails.billingaddresszip || '',
        billingAddressCity: clientDetails.billingaddresscity || '',
        billingAddressStateOthers: clientDetails.billingaddressstateothers || '',
        placeOfSupplyOther: clientDetails.placeofsupplyothers || ''
      })
      setConatctId(clientDetails.id)
      setCountryCode(clientDetails.billingaddresscountriesshorcode)

      handleEmployeeChange(clientDetails?.employeelist)
    }

    if (contactId !== null) {
      getClientInfo()
    }

  }, [contactId, clientDetails])

  useEffect(() => {
    store.clientInformations.forEach((obj) => {
      append(obj)
    })
  }, [store.clientInformations])

  useEffect(() => {
    if (clientInfo.length) {
      saveContactInfo()
    } 
  }, [clientInfo])

  useEffect(() => {
    getBusineessEntity()
    getOrganizationUsers()
    getCountries()
    getCurrency()
    getGSTRegType()
    getStates()

    getClientData()

  }, [])


  const getRow = (fieldLabel, fieldName, reqflag = false, colFlag = true) => {
    return (
      <Row className='mb-0 mb-lg-1'>
        <Label sm={colFlag ? '4' : '12'} size='lg' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col sm={colFlag ? '8' : '12'}>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Row>
    )
  }

  const getSelectRow = (fieldLabel, fieldName, options, reqflag = false, colFlag = true) => {
    return (

      <Row className='mb-0 mb-lg-1'>
        <Label sm={colFlag ? '4' : '12'} size='lg' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col sm={colFlag ? '8' : '12'}>
          <Controller
            control={control}
            name={fieldName}
            id={fieldName}
            render={({ field, ref }) => (
              <Select
                inputRef={ref}
                className={classnames('react-select', { 'is-invalid': errors[fieldName] })}
                {...field}
                classNamePrefix='select'
                options={options}
                value={options.find(c => { return c.id === field.value })}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={val => {
                  if (fieldName === 'billingAddressCountry') {
                    setCountryCode(val.shortname)
                    setValue('billingAddressState', null)
                    setValue('placeOfSupply', null)
                    setValue('billingCountryCode', val.shortname)
                  }
                  return field.onChange(val.id)
                }}
              />
            )}

          />
          {errors[fieldName] && <FormFeedback className='text-danger'>{errors[fieldName]?.message}</FormFeedback>}
        </Col>
      </Row>

    )
  }
  const renderError = () => {
    const keys = Object.keys(errors)
    const ErrorText = []
    if (Array.isArray(keys)) {
      keys.forEach((key) => {
        if (key !== 'rows') {
          ErrorText.push(<FormFeedback key={key}>${errors[key]?.message}</FormFeedback>)
        }
      })
    }
    return ErrorText
  }

  const setPrimaryDetails = (fieldName, value, index) => {

    if (fieldName === 'primarystatus' && value.target.checked) {
      setValue('contactPersonName', control._formValues.contact_info[index].name)
      setValue('contactNumber', control._formValues.contact_info[index].contactnumber)
      setValue('email', control._formValues.contact_info[index].email)
    } else {
      if (control._formValues.contact_info[index].primarystatus) {
        if (fieldName === 'primaryStatus') {
          setValue(fieldName, value.target.checked)
        } else {
          setValue(fieldName, value)
        }
      }
    }
  }

  return (
    <UILoader blocking={block} loader={<Loader />}>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Card className='invoice-preview-card'>
          {/* Header */}
          <CardHeader>
            <CardTitle> Edit Client</CardTitle>
            {
              renderError()
            }
          </CardHeader>
          <CardBody className='pb-0'>
            <Row>
              <Col md='4' className='mb-0 mb-lg-1'>
                {getRow('Business Name', 'name', true, false)}
              </Col>
              <Col md='4' className='mb-0 mb-lg-1'>
                {getSelectRow('Business Entity', 'businessEntity', businessEntityOptions, true, false)}
              </Col>
              <Col md='4' className='mb-0 mb-lg-1'>
                {getRow('Registration No', 'registrationNo', false, false)}
              </Col>
            </Row>
            <Row className=''>
              <Col md='4' className='mb-0 mb-lg-1'>
                <Label sm={'12'} lg='12' size='lg' className={classnames(`form-label`)} >
                  Assign User
                </Label>
                <Col sm={'12'} lg='12' >
                  <Controller
                    control={control}
                    name="employeeList"
                    id="employeeList"
                    render={({ field, ref }) => (
                      <Select
                        inputRef={ref}
                        className={classnames('react-select', { 'is-invalid': errors.employeeList })}
                        {...field}
                        classNamePrefix='select'
                        options={userOptions}
                        isMulti={true}
                        onChange={handleEmployeeChange1}
                        value={employeeDefaultValue}
                        getOptionLabel={(option) => option.name}
                        getOptionValue={(option) => option.id}
                        styles={colourStyles}
                      />
                    )}

                  />
                  {errors.employeeList && <FormFeedback className='text-danger'>{errors.employeeList?.message}</FormFeedback>}
                </Col>
              </Col>
            </Row>

          </CardBody>
          {/* /Header */}

          <hr className='invoice-spacing' />
          {errors.contact_info && <p className='text-danger ms-2'>{errors.contact_info?.message}</p>}
          {/* Product Details */}
          <CardBody className='invoice-padding invoice-product-details p-1'>
            {fields.map((item, i) => (

              <div key={item.id} className='repeater-wrapper'>
                <Row >
                  <Col className='d-lg-flex justify-content-between product-details-border position-relative' sm='12' lg='12' md='12' >
                    <Row className='align-items-center p-75'>
                      <Col className='p-50 col-6 col-lg-2 col-md-2' >
                        <Label className='form-label mt-1'>PAN</Label>
                        <Controller
                          control={control}
                          id='contact_info_pannumber'
                          name={`contact_info[${i}].pannumber`}
                          render={({ field }) => (
                            <Input type='text' placeholder='PAN' onChange={(val) => { field.onChange(val) }} invalid={errors.contact_info?.[i]?.pannumber && true} {...field} />
                          )}
                        />
                        {errors.contact_info?.[i]?.pannumber && <FormFeedback>{errors.contact_info?.[i]?.pannumber.message}</FormFeedback>}
                      </Col>
                      <Col className='p-50 col-6 col-lg-2 col-md-2'>
                        <Label className='form-label mt-1'>Name</Label>
                        <Controller
                          control={control}
                          id='contact_info_firstName'
                          name={`contact_info[${i}].name`}
                          render={({ field }) => (
                            <Input type='text' placeholder='Name' onInput={(e) => { setPrimaryDetails('contactPersonName', e.target.value, i) }} onChange={(val) => { field.onChange(val) }} invalid={errors.contact_info?.[i]?.name && true} {...field} />
                          )}
                        />
                        {errors.contact_info?.[i]?.name && <FormFeedback>{errors.contact_info?.[i]?.name.message}</FormFeedback>}
                      </Col>
                      <Col className='p-50 col-6 col-lg-2 col-md-2'>
                        <Label className='form-label mt-1'>Mobile</Label>
                        <Controller
                          control={control}
                          id='contact_info_contactnumber'
                          name={`contact_info[${i}].contactnumber`}
                          render={({ field }) => (
                            <Input type='number' placeholder='Mobile' onInput={(e) => { setPrimaryDetails('contactnumber', e.target.value, i) }} onChange={(val) => { field.onChange(val) }} invalid={errors.contact_info?.[i]?.contactnumber && true} {...field} />
                          )}
                        />
                        {errors.contact_info?.[i]?.contactnumber && <FormFeedback>{errors.contact_info?.[i]?.contactnumber.message}</FormFeedback>}
                      </Col>
                      <Col className='p-50 col-6 col-lg-2 col-md-2'>
                        <Label className='form-label mt-1'>Email</Label>
                        <Controller
                          control={control}
                          id='contact_info_email'
                          name={`contact_info[${i}].email`}
                          render={({ field }) => (
                            <Input type='email' placeholder='Email' onInput={(e) => { setPrimaryDetails('email', e.target.value, i) }} onChange={(val) => { field.onChange(val) }} invalid={errors.contact_info?.[i]?.email && true} {...field} />
                          )}
                        />
                        {errors.contact_info?.[i]?.email && <FormFeedback>{errors.contact_info?.[i]?.email.message}</FormFeedback>}
                      </Col>
                      <Col className='p-50 col-6 col-lg-2 col-md-2'>
                        <Label className='form-label mt-1'>Designation</Label>
                        <Controller
                          control={control}
                          id='contact_info_designation'
                          name={`contact_info[${i}].designation`}
                          render={({ field }) => (
                            <Input type='text' placeholder='Designation' onChange={(val) => { field.onChange(val) }} invalid={errors.contact_info?.[i]?.designation && true} {...field} />
                          )}
                        />
                        {errors.contact_info?.[i]?.designation && <FormFeedback>{errors.contact_info?.[i]?.designation.message}</FormFeedback>}
                      </Col>
                      <Col className='p-50 col-6 col-lg-2 col-md-2'>
                        <Label className='form-label mt-1'>Aadhaar</Label>
                        <Controller
                          control={control}
                          id='contact_info_adharnumber'
                          name={`contact_info[${i}].adharnumber`}
                          render={({ field }) => (
                            <Input type='text' placeholder='Adhaar' onChange={(val) => { field.onChange(val) }} invalid={errors.contact_info?.[i]?.adharnumber && true} {...field} />
                          )}
                        />
                        {errors.contact_info?.[i]?.adharnumber && <FormFeedback>{errors.contact_info?.[i]?.adharnumber.message}</FormFeedback>}
                      </Col>
                      <Col className='p-50 col-6 col-lg-2 col-md-2'>
                        <Label className='form-label mt-1'>DIN</Label>
                        <Controller
                          control={control}
                          id='contact_info_dinnumber'
                          name={`contact_info[${i}].dinnumber`}
                          render={({ field }) => (
                            <Input type='text' placeholder='DIN' onChange={(val) => { field.onChange(val) }} invalid={errors.contact_info?.[i]?.dinnumber && true} {...field} />
                          )}
                        />
                        {errors.contact_info?.[i]?.dinnumber && <FormFeedback>{errors.contact_info?.[i]?.dinnumber.message}</FormFeedback>}
                      </Col>
                      <Col className='p-50 col-6 col-lg-2 col-md-2'>
                        <Label className='form-label mt-1'>Primary</Label>

                        <div className='form-switch form-check-primary'>
                          <Controller
                            control={control}
                            id='contact_info_primaryStatus'
                            name={`contact_info[${i}].primarystatus`}
                            render={({ field }) => (

                              <Input type='switch' value={field.value} onInput={(val) => { setPrimaryDetails('primarystatus', val, i) }} {...field} defaultChecked={field.value} />
                            )}
                          />
                        </div>
                      </Col>
                      {
                        control._formValues.contact_info.length > 1 &&
                        <div className='d-lg-flex width-50 justify-content-center border-start invoice-product-actions py-50 px-25'>
                          <X size={18} className='cursor-pointer' onClick={() => { removeItem(i) }} />
                        </div>
                      }
                    </Row>
                  </Col>
                </Row>
              </div>
            ))}

            <Row className='mt-1'>
              <Col sm='12' className='px-1'>
                <Button color='primary' size='sm' className='btn-add-new' onClick={() => addItem()}>
                  <Plus size={14} className='me-25'></Plus> <span className='align-middle'>Add Item</span>
                </Button>
              </Col>
            </Row>

          </CardBody>

          <hr className='invoice-spacing' />
          {/* Product Details */}
          <CardBody>
            {countryCode === 'IN' &&
              <Row>
                <Col md='6' className='mb-0 mb-lg-1'>
                  {getSelectRow('GST Type', 'gstRegistrationType', gstRegistrationTypeOptions)}
                </Col>
                <Col md='6' className='mb-0 mb-lg-1'>
                  {getRow('GSTIN', 'gstin')}
                </Col>
              </Row>
            }
            <Row>
              <Col md='6' className='mb-0 mb-lg-1'>
                {countryCode === 'IN' ? getSelectRow('Place of Supply', 'placeOfSupply', stateOptions) : getRow('Place of Supply', 'placeOfSupplyOther')}
              </Col>
              <Col md='6' className='mb-0 mb-lg-1'>
                {getSelectRow('Currency', 'currency', currencyOptions, true)}
              </Col>
            </Row>
          </CardBody>
          {/* Invoice Total */}
          <CardBody className=''>
            <h4 className='text-primary mt-50 mt-lg-0'>Billing Address</h4>
            <Row>
              <Col md='6' className='mb-0 mb-lg-1'>
                {getRow('Address Line1', 'billingAddressLine1')}
              </Col>
              <Col md='6' className='mb-0 mb-lg-1'>
                {getRow('Address Line2', 'billingAddressLine2')}
              </Col>
            </Row>

            <Row>
              <Col md='6' className='mb-0 mb-lg-1'>
                {getRow('City', 'billingAddressCity')}
              </Col>
              <Col md='6' className='mb-0 mb-lg-1'>
                {countryCode === 'IN' ? getSelectRow('State', 'billingAddressState', stateOptions) : getRow('State', 'billingAddressStateOthers')}
              </Col>
            </Row>

            <Row>
              <Col md='6' className='mb-0 mb-lg-1'>
                {getSelectRow('Country', 'billingAddressCountry', countryOptions, true)}
              </Col>
              <Col md='6' className='mb-0 mb-lg-1'>
                {getRow('Zip Code', 'billingAddressZip')}
              </Col>
            </Row>
          </CardBody>
        </Card>
        <Card>
          <CardBody>
            <div className='modal-footer border-0'>
              <Button color='warning' outline tag={Link} to={`/clients/view/${id}`}>
                Cancel
              </Button>
              <Button color='primary' type="submit" >
                Save
              </Button>
            </div>
          </CardBody>
        </Card>
      </form >
    </UILoader>
  )
}

export default EditCard
